// import CommentList from "./commentsidebar/CommentList";
import "./PostCommentContent.css";
import "./commentsidebar/WriteComment.css";
import "./commentsidebar/CommentList.css";
import axios from "axios";
import { useParams } from "react-router-dom";
import { useState, useEffect } from "react";

export default function PostCommentContent() {
  const { id: postId } = useParams();
  // id:postid 
  // id  
  const authorization = window.sessionStorage.getItem("Authorization");

  const [content, setContent] = useState(""); // 
  const [comments, setComments] = useState([]); // 

  const [editingCommentId, setEditingCommentId] = useState(null); //    ID
  const [editingContent, setEditingContent] = useState(""); //    

  //=== ===
  const handleSubmin = (e) => {
    e.preventDefault();
    if (!content) {
      alert("  ."); //   
      return;
    }

    try {
      const commentwite = async () => {
        const response = await axios.post(
          `http://localhost:8000/api/posts/${postId}/comments`,
          { content },
          {
            headers: {
              Authorization: authorization,
            },
          }
        );
        setContent("");
        // console.log(" :",data);
      };

      setComments([...comments]);
      commentwite();
    } catch (error) {}
  };

  //=== ===
  useEffect(() => {
    const findComments = async () => {
      try {
        const response = await axios.get(
          `http://localhost:8000/api/posts/${postId}/comments`,
          {}
        );
        setComments(response.data);
      } catch (error) {}
    };
    findComments();
  }, [content]);

  //   
  const startEditing = (id, currentContent) => {
    setEditingCommentId(id);
    setEditingContent(currentContent);
  };

  //   
  const saveEditing = async (id) => {
    try {
      const response = await axios.patch(
        `http://localhost:8000/api/posts/${postId}/comments/${id}`,
        { content: editingContent },
        {
          headers: {
            Authorization: authorization,
          },
        }
      );

      setComments(
        comments.map((comment) =>
          comment.id === id
            ? { ...comment, content: response.data.content }
            : comment
        )
      );
      setEditingCommentId(null); //   
    } catch (error) {}
  };

  //   
  const cancelEditing = () => {
    setEditingCommentId(null);
    setEditingContent("");
  };

  return (
    <div className="PostCommentContent">
      {/* ======== */}
      <div className="WriteContent">
        <form onSubmit={handleSubmin}>
          <input
            value={content}
            type="text"
            onChange={(e) => setContent(e.target.value)}
            placeholder="  ."
          />
          <button type="submit"></button>
        </form>
      </div>

      {/* ============ */}
      <div className="CommentList">
        {comments.map((comm) => {
          const { content, nickname, createdAt } = comm;
          return (
            <div key={comm.id} className="CommentListContent">
              <div className="CLCdetail">
                {editingCommentId === comm.id ? (
                  <div>
                    <input
                      value={editingContent}
                      onChange={(e) => setEditingContent(e.target.value)}
                    />
                    <button onClick={() => saveEditing(comm.id)}></button>
                    <button onClick={cancelEditing}></button>
                  </div>
                ) : (
                  <>
                    <h3>{content}</h3>
                    <p>{nickname}</p>
                    <p>{createdAt.split("T")[0]}</p>
                    <div className="CLCbutton">
                      <button
                        className="CLCReportComment"
                        onClick={(e) => {
                          e.preventDefault();
                          window.location.href = `http://127.0.0.1:3000/#reports/#comments/#${comm.id}`;
                        }}
                      >
                        
                      </button>

                      {/*   */}
                      {editingCommentId !== comm.id && (
                        <button onClick={() => startEditing(comm.id, content)}>
                          
                        </button>
                      )}

                      {/* ===== */}
                      <button
                        onClick={async () => {
                          if (window.confirm(" ?")) {
                            try {
                              await axios.delete(
                                `http://localhost:8000/api/posts/${postId}/comments/${comm.id}`,
                                {
                                  headers: {
                                    Authorization: authorization,
                                  },
                                }
                              );
                              setComments(
                                comments.filter(
                                  (comment) => comment.id !== comm.id
                                )
                              );
                            } catch (error) {}
                          }
                        }}
                      >
                        
                      </button>
                    </div>
                  </>
                )}
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
}
