import React, { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import axios from "axios";
import Header from "../Header";
import Footer from "../Footer";
import PostImgContent from "./PostImgContent";
import PostCommentContent from "./PostCommentContent";

const Post = () => {
  const { id } = useParams(); // URL ID 
  const [data, setData] = useState(null); // API  
  const [loading, setLoading] = useState(true); //  
  const [error, setError] = useState(null); //  

  useEffect(() => {
    const fetchPost = async () => {
      try {
        setLoading(true); //  
        const result = await axios.get(`http://127.0.0.1:8000/api/posts/${id}`);
        setData(result.data); //  
      } catch (err) {
        setError(err); //  
      } finally {
        setLoading(false); //  
      }
    };

    fetchPost();
  }, [id]);



  if (loading) return <p>Loading...</p>; //   
  if (error) return <p>Error: {error.message}</p>; //  

  return (
    <div className="post">
      <Header />
      <div className="main-content">
        <PostImgContent data={data} />
        <PostCommentContent data={data} />
      </div>

      <Footer />
    </div>
  );
};

export default Post;
