import Footer from "../Footer";
import Header from "../Header";
import "../users/SignUp.css";
import React, { useEffect, useState } from "react";

export default function UserUpdate() {
  const [email, setEmail] = useState("");
  const [beforeNickname, setBeforeNickname] = useState("");
  const [nickname, setNickname] = useState("");
  const [id, setId] = useState("");

  const token = window.sessionStorage.getItem("Authorization");

  useEffect(() => {
    //    API 
    const fetchData = async () => {
      fetch("http://localhost:8000/api/users", {
        method: "GET",
        headers: {
          Authorization: token,
        },
      })
        .then((response) => {
          if (!response.ok) {
            throw new Error("   .");
          }
          return response.json();
        })
        .then((data) => {
          setEmail(data.email);
          setBeforeNickname(data.nickname);
          setId(data.id);
        })
        .catch((error) => {});
    };

    fetchData();
  }, []); //          

  const handleInputChange = (e) => {
    setNickname(e.target.value.trim());
  };

  const handleUserEditSubmit = (e) => {
    e.preventDefault(); //     

    fetch(`http://localhost:8000/api/users/${id}`, {
      method: "PATCH", // POST 
      headers: {
        "Content-Type": "application/json",
        Authorization: token,
      },
      body: JSON.stringify({ nickname }),
    })
      .then((response) => {
        if (!response.ok) {
          alert("   .");
        }
        return response.json();
      })
      .then((data) => {
        alert("  .");
        window.location.href = "/#mypage";
      })
      .catch((error) => {});
  };

  return (
    <div>
      <Header />
      <div className="container">
        <div className="form-container">
          <h1>  </h1>
          <div className="form-row">
            <label>E-mail</label>
            <input type="text" id="email" value={email} disabled></input>
          </div>
          <div className="form-row">
            <label></label>
            <input
              type="text"
              id="nickname"
              value={nickname}
              onChange={handleInputChange}
              placeholder={beforeNickname}
            ></input>
          </div>
          <button className="special-button" onClick={handleUserEditSubmit}>
            
          </button>
        </div>
      </div>
      <Footer />
    </div>
  );
}
