import Footer from "../Footer";
import Header from "../Header";
import React, { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";

export default function GroupUpdate() {
  const [groupName, setGroupName] = useState("");
  const [description, setDescription] = useState("");
  const token = window.sessionStorage.getItem("Authorization");

  const location = useLocation(); // useLocation  state 
  const data = location.state; //  state 

  useEffect(() => {
    //    API 
    const fetchData = async () => {
      fetch(`http://localhost:8000/api/groups/${data.id}`, {
        method: "GET",
        headers: {
          Authorization: token,
        },
      })
        .then((response) => {
          if (!response.ok) {
            throw new Error("   .");
          }
          return response.json();
        })
        .then((data) => {
          setGroupName(data.groupName);
          setDescription(data.description);
        })
        .catch((error) => {});
    };

    fetchData();
  }, []); //          

  const handleInputChange = (e) => {
    if ("groupName" == e.target.id) {
      setGroupName(e.target.value.trim());
    }
    if ("description" == e.target.id) {
      setDescription(e.target.value.trim());
    }
  };

  const handleSubmit = (e) => {
    e.preventDefault(); //     

    fetch(`http://localhost:8000/api/groups/${data.id}`, {
      method: "PATCH",
      headers: {
        "Content-Type": "application/json",
        Authorization: token,
      },
      body: JSON.stringify({ groupName, description }),
    })
      .then((response) => {
        if (response.ok) {
          alert(" .");
        } else {
          alert(" .");
        }
        return response.json();
      })
      .then((data) => {
        window.location.href = "/#groups";
      })
      .catch((error) => {});
  };

  return (
    <div>
      <Header />
      <div className="container">
        <div className="form-container">
          <h1> </h1>
          <div className="form-row">
            <label> </label>
            <input
              type="text"
              id="groupName"
              className="input2"
              value={groupName}
              onChange={handleInputChange}
            ></input>
          </div>
          <div className="form-row">
            <label> </label>
            <input
              type="text"
              id="description"
              className="input2"
              value={description}
              onChange={handleInputChange}
            ></input>
          </div>
          <button onClick={handleSubmit}> </button>
        </div>
      </div>
      <Footer />
    </div>
  );
}
