import React, { useState, useEffect } from "react";
import { Radar } from "react-chartjs-2";
import {
  Chart as ChartJS,
  RadarController,
  RadialLinearScale,
  CategoryScale,
  LinearScale,
  Title,
  Tooltip,
  Legend,
  PointElement,
  LineElement,
} from "chart.js";
import axios from "axios";

//  Chart.js  
ChartJS.register(
  RadarController,
  RadialLinearScale,
  CategoryScale,
  LinearScale,
  Title,
  Tooltip,
  Legend,
  PointElement,
  LineElement
);

export default function Chart() {
  const [data, setData] = useState(null); //   
  const [isLoading, setIsLoading] = useState(true); //  

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await axios.get(
          "http://127.0.0.1:8000/api/statistics/category",
          {
            headers: {
              Authorization: window.sessionStorage.getItem("Authorization"),
            },
          }
        );

        //    
        const labels = [];
        const datas = [];
        response.data.forEach((item) => {
          labels.push(item.category);
          datas.push(Number(item.count)); //   
        });

   

        //   
        setData({
          labels, //     
          datasets: [
            {
              label: " ",
              data: datas, //    (   )
              fill: true, //    
              backgroundColor: "rgba(75, 192, 192, 0.2)", //  
              borderColor: "rgb(75, 192, 192)", //  
              borderWidth: 2, //  
            },
          ],
        });
      } catch (error) {

      } finally {
        setIsLoading(false); //   
      }
    };

    fetchData();
  }, []); //      

  //     
  if (isLoading) {
    return <div>Loading...</div>;
  }

  //    
  if (!data) {
    return <div>Failed to load chart data.</div>;
  }

  //  
  const options = {
    responsive: true,
    scale: {
      ticks: {
        beginAtZero: true, //  0  
      },
    },
    plugins: {
      legend: {
        position: "top",
      },
      tooltip: {
        mode: "index",
        intersect: false,
      },
    },
  };

  return (
    <div>
      <Radar data={data} options={options} />
    </div>
  );
}
