import Footer from "../Footer";
import Header from "../Header";
import "./SignUp.css";
import React, { useState } from "react";

export default function SignUp() {
  const [email, setEmail] = useState(""); //   
  const [number, setNumber] = useState("");
  const [nickname, setNickname] = useState("");
  const [password, setPassword] = useState("");
  const [confirm, setConfirm] = useState("");

  const handleInputChange = (e) => {
    if ("email" == e.target.id) {
      setEmail(e.target.value.trim()); // input     
    }
    if ("number" == e.target.id) {
      setNumber(e.target.value.trim());
    }
    if ("nickname" == e.target.id) {
      setNickname(e.target.value.trim());
    }
    if ("password" == e.target.id) {
      setPassword(e.target.value.trim());
    }
    if ("confirm" == e.target.id) {
      setConfirm(e.target.value.trim());
    }
  };
  const handleEmailSubmit = (e) => {
    e.preventDefault(); //     d

    fetch("http://localhost:8000/api/auth/email", {
      method: "POST", // POST 
      headers: {
        "Content-Type": "application/json", // JSON  
      },
      body: JSON.stringify({ email }), //  JSON  
    })
      .then((response) => {
        if (!response.ok) {
          throw new Error("   .");
        }
        return response;
      })
      .then((data) => {
        alert("    ");
      })
      .catch((error) => {});
  };

  const handleSignupSubmit = (e) => {
    e.preventDefault(); //     

    fetch("http://localhost:8000/api/auth/signup", {
      method: "POST", // POST 
      headers: {
        "Content-Type": "application/json", // JSON  
      },
      body: JSON.stringify({ email, nickname, password }), //  JSON  
    })
      .then((response) => {
        if (!response.ok) {
          throw new Error("   .");
        }
        return response.json();
      })
      .then((data) => {
        alert(" .");
        window.sessionStorage.setItem("Authorization", data.accessToken);
        window.location.href = "/";
      })
      .catch((error) => {});
  };

  const handleVerifySubmit = (e) => {
    e.preventDefault(); //     

    fetch("http://localhost:8000/api/auth/verify", {
      method: "POST", // POST 
      headers: {
        "Content-Type": "application/json", // JSON  
      },
      body: JSON.stringify({ email, number }), //  JSON  
    })
      .then((response) => {
        if (!response.ok) {
          throw new Error("   .");
        }
        return response;
      })
      .then((data) => {
        alert(".");
      })
      .catch((error) => {});
  };
  return (
    <div>
      <Header />
      <div className="container">
        <div className="form-container">
          <h1></h1>
          <div className="form-row">
            <label>E-mail</label>
            <input
              type="text"
              id="email"
              value={email}
              onChange={handleInputChange}
            ></input>
            <button onClick={handleEmailSubmit}></button>
          </div>
          <div className="form-row">
            <label></label>
            <input
              type="text"
              id="number"
              value={number}
              onChange={handleInputChange}
            ></input>
            <button onClick={handleVerifySubmit}></button>
          </div>
          <div className="form-row">
            <label></label>
            <input
              type="text"
              id="nickname"
              onChange={handleInputChange}
            ></input>
          </div>
          <div className="form-row">
            <label></label>
            <input
              type="password"
              id="password"
              onChange={handleInputChange}
            ></input>
          </div>
          <div className="form-row">
            <label> </label>
            <input
              type="password"
              id="confirm"
              onChange={handleInputChange}
            ></input>
          </div>
          <button className="signup-button" onClick={handleSignupSubmit}>
              
          </button>
        </div>
      </div>
      <Footer />
    </div>
  );
}
